@font-face {
  font-family: 'OldEnglishTextMT2';
  src: url(./assets/fonts/OldEnglishTextMT2/OldEnglishTextMT2.ttf);
}

html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  font-family: OldEnglishTextMT2;
  display: flex;
  justify-content: center;
  font-size: 62.5%;
}

ins {
  text-decoration: none;
}

a {
  color: black;
}

#root {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.main {
  font-size: 1rem;
  display: flex;
  justify-content: center;
  text-align: justify;
  align-items: flex-start;
  position: relative;
  // background-color: #dfd0b3;
  background-image: radial-gradient(#dfd0b3, #e9d3b5);
  flex-basis: 100%;
  // flex-wrap: wrap;
  max-width: 85%;
  // padding: 1rem 2rem;
  // margin: 1rem;
  box-sizing: border-box;

  .text {
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    margin-right: 1rem;
    
    .title {
      text-align: center;
      flex-basis: 100%;
      margin: 0;
      margin-bottom: 2rem;
      padding: 0;
      font-size: 5rem;
    }
  
    .medium-title {
      text-align: center;
      flex-basis: 100%;
      margin: 0;
      margin-bottom: 2rem;
      padding: 0;
      font-size: 3rem;
    }

    .full-paragraph {
      font-size: 2rem;
      align-items: flex-start;
      margin-bottom: 2rem;
      // flex-basis: 100%;
      // width: 100%;
  
      .title {
        font-size: 2.2rem;
        text-align: left;
        margin: 0;
      }
    }
  }
}

@media only screen and (max-width: 600px) {
  html, body {
    font-size: 40.3%;
  }

  .main {
    max-width: 100%;
    width: 100%;
    padding-right: 10rem;
  }
}